import React from "react";
import Alert from 'react-bootstrap/Alert';
import {Card} from "react-bootstrap";
import ExplainData from "../components/explainData";

export default function FirstTeamPage() {
	return (
		<div className="text-center">
			<h1 className="text-center QATitle">  </h1>
			<Warning/>
			<QADrawer/>
			{/* <ExplainData/> */}
		</div>
	);
}



function Warning() {
  return (
    <div>
        <Alert key="warning" variant="warning">
          
        </Alert>

    </div>
  );
}


function QA(){
	this.Questions = []
	this.Answers = []
	this.add = function(Q,A){
		this.Questions.push(Q)
		this.Answers.push(A)
	}
}

let QAs = new QA()
QAs.add(""," 0~10 10")
QAs.add("","")


function QADrawer(){
	let res=[]
	for (let i = 0; i < QAs.Questions.length; i++) {
		res.push(<QABlock q={QAs.Questions[i]} a={QAs.Answers[i]} />)
		res.push(<br></br>)
	}
	return(
		<ul>
			{res}
		</ul>
	)
}


function QABlock(props) {
	return (
		<Card>
		  <Card.Header as="h5">{props.q}</Card.Header>
		  <Card.Body>
			<Card.Text >
				{props.a}
			</Card.Text>
			
		  </Card.Body>
		</Card>
	  );
  }